import axios from 'axios'

// const baseurl = 'http://193.0.10.163:20002'

const baseurl = 'http://193.0.1.248:20002'

const Rxports = {

  /**
     * axiosjq ajax
     * @param {String} type			post
     * @param {String} url				
     * @param {String} time			
     * @param {Object} data			
     * @param {String} dataType		xml html json ...
     * @param {Object} headers			headers
     * @param {Function} success		,[data, res]
     * @param {Function} error		
     * @param return
     */
  ajax: function (opt) {
    const opts = opt || {}

    if (!opts.url) {
      alert('')
      return false
    }

    axios({
      method: opts.type || 'get',
      url: opts.url,
      params: opts.data || {},
      headers: opts.headers || {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      // `baseURL`  `url`  `url`  URL
      //  `baseURL`  axios  URL
      baseURL: baseurl,
      timeout: opts.time || 10 * 1000,
      responseType: opts.dataType || 'json'
    }).then(function (res) {
      if (res.status === 200) {
        if (opts.success) {
          opts.success(res.data, res)
        }
      }
    }).catch(function (error) {
      if (opts.error) {
        opts.error(error)
      } else {
        console.log('')
      }
    })
  },

  /**
     * url
     * @param name 	
     * @param Url 		
     * @param return
     */
  getUrlQuery: function (name, Url) {
    // URL GET 
    const reg = new RegExp('(^|\\?|&)' + name + '=([^&]*)(\\s|&|$)', 'i')
    const url = Url || location.href
    if (reg.test(url)) { return unescape(RegExp.$2.replace(/\+/g, ' ')) }
    return ''
  }

}

export default Rxports




